.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  opacity: 1;
  transition: opacity var(--transition), visibility var(--transition);
  background-color: #0000001a;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal-wrapper {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.3);
}

.modal-wrapper {
  opacity: 1;
  transition: transform var(--transition) 200ms, opacity var(--transition) 200ms;
  background-color: #fff;
  border-radius: 4px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

